import React, { useEffect, useState } from 'react';

import Grid from '@mui/material/Grid2';
import { FormControl, InputLabel, Select, MenuItem, 
    TextField, Button, Paper, Typography, 
    Stack,
    Chip,
    stepButtonClasses, Box,
    ButtonGroup} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import DeleteIcon from '@mui/icons-material/Delete';
import { alignProperty } from '@mui/material/styles/cssUtils';
import { grey } from '@mui/material/colors';

function DeviceConnection( {onConnect, port, setPort} ) {
    const [isConnected, setIsConnected] = useState(false);
    const [connectBaudRate, setconnectBaudRate] = useState(9600);
    const [dataBits, setDataBits] = useState(8);
    const [stopBits, setStopBits] = useState(1);
    const [parity, setParity] = useState('none');

    //    
    useEffect(() => {
        const savedConnectSettings = localStorage.getItem('serialConnectSettings');
        if(savedConnectSettings){
            const { connectBaudRate, dataBits, stopBits, parity } = JSON.parse(savedConnectSettings);
            setconnectBaudRate(connectBaudRate);
            setDataBits(dataBits);
            setStopBits(stopBits);
            setParity(parity);
        }
    }, []);

    const connectDevice = async () => {
        try {
            const port = await navigator.serial.requestPort();
            await port.open({ 
                baudRate: Number(connectBaudRate),
                dataBits: Number(dataBits),
                stopBits: Number(stopBits),
                parity: parity,
                flowControl: 'none' //   
            });
            setIsConnected(true);
            onConnect(port);

            //    
            port.addEventListener('disconnect', () => {
                setPort(null);
            });

            //  
            localStorage.setItem('serialConnectSettings', JSON.stringify(
                {connectBaudRate, dataBits, stopBits, parity}
            ));
        } catch (error) {
            console.error('! Error connecting:', error);
        }
    };

    const disconnectDevice = async () => {
        console.log("  ");
        if(!port) return;

        if(port.readable || port.writable) {
            try {
                await port.close();
            } catch (error) {
                console.error('! Error disconnecting:', error);
                alert('   .');
            }
        }
    }

    //     Modal
    const handleShowPortInfo = async () => {
        console.log("   ");
        if(!port) {
            alert('    .');
            return
        } 
            
        const info = await port.getInfo();
        alert(` \n\n${JSON.stringify(info, null, 2)}`);
    }

    return (
        <Box p={2} display='flex' justifyContent='center' alignItems='center'>
            <Grid container spacing={2} sx={{justifyContent:'center'}}
                rowSpacing={0}>
                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:100}}>
                        <InputLabel id="baudRate-label">BaudRate</InputLabel>
                        <Select
                            labelId="baudRate-label"
                            label="BaudRate"
                            id="baudRate"
                            size='small'
                            value={connectBaudRate}
                            onChange={(e) => setconnectBaudRate(Number(e.target.value))}
                        >
                            {[1200, 2400, 4800, 9600, 19200, 38400, 57600, 115200].map((baudRate) => (
                                <MenuItem key={baudRate} value={baudRate}>{
                                    baudRate}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>
            
                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:50}}>
                        <InputLabel id="dataBits-label">dataBits</InputLabel>
                        <Select
                            labelId="dataBits-label"
                            label="dataBits"
                            id="dataBits"
                            size='small'
                            value={dataBits}
                            onChange={(e) => setDataBits(Number(e.target.value))}
                        >
                            {[8, 7].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:50}}>
                        <InputLabel id="stopBits-label">stopBits</InputLabel>
                        <Select
                            labelId="stopBits-label"
                            label="stopBits"
                            id="stopBits"
                            size='small'
                            value={stopBits}
                            onChange={(e) => setStopBits(Number(e.target.value))}
                        >
                            {[1, 2].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:90}}>
                        <InputLabel id="parity-label">parity</InputLabel>
                        <Select
                            labelId="parity-label"
                            label="parity"
                            id="parity"
                            size='small'
                            value={parity}
                            onChange={(e) => setParity(e.target.value)}
                        >
                            {["none", "even", "odd"].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <ButtonGroup variant='contained' size='small' sx={{m:1.5}}>
                        <Button endIcon={<SendIcon />} onClick={connectDevice}> </Button>
                        <Button endIcon={<DeleteIcon />} onClick={disconnectDevice} disabled={!port}> </Button>
                        <Button
                            variant='text'
                            onClick={handleShowPortInfo}
                            color={isConnected ? 'success' : 'warning'}
                        > </Button>
                    </ButtonGroup>
                </Grid>
        </Grid>
            
        </Box>
    );
}

export default DeviceConnection;